"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import api from "../lib/api";
import { getToken } from "../lib/auth";
import Layout from "../components/Layout";
import ChatSubnav from "../components/ChatSubnav";
import TypewriterText from "../components/TypewriterText";
import ChatListItem from "../components/ChatListItem";

export default function ChatPage() {
  const router = useRouter();
  const [chats, setChats] = useState([]);
  const [selectedChat, setSelectedChat] = useState(null);
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [typingMessageId, setTypingMessageId] = useState(null);
  const [showArchived, setShowArchived] = useState(false);

  // Protect route
  useEffect(() => {
    const token = getToken();
    if (!token) router.push("/login");
    fetchChats();
  }, []);

  const fetchChats = async () => {
    const res = await api.get("/chats", {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
    setChats(res.data);
  };

 const startNewChat = () => {
  setSelectedChat({ _id: null, title: "New Chat (unsaved)" });
  setMessages([]);
};

  const openChat = async (chat) => {
    const res = await api.get(`/chats/${chat._id}`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
    setSelectedChat(res.data);
    setMessages(res.data.messages);
  };

//   const sendMessage = async () => {
//   if (!input.trim()) return;

//   const userMsg = { role: "user", content: input };
//   setMessages((prev) => [...prev, userMsg]);
//   setInput("");
//   setLoading(true);

//   try {
//     let reply;

//     // if chat not yet created
//     if (!selectedChat || !selectedChat._id) {
//       const res = await api.post(
//         "/chats/new",
//         { message: input }, // send first message here
//         { headers: { Authorization: `Bearer ${getToken()}` } }
//       );
//       setSelectedChat(res.data.chat);
//       reply = res.data.reply;
//     } else {
//       const res = await api.post(
//         `/chats/${selectedChat._id}/message`,
//         { message: input },
//         { headers: { Authorization: `Bearer ${getToken()}` } }
//       );
//       reply = res.data.reply;
//     }

//     setMessages((prev) => [...prev, { role: "ai", content: reply }]);
//     fetchChats(); // refresh sidebar
//   } catch (err) {
//     console.error("Send message failed:", err);
//   } finally {
//     setLoading(false);
//   }
// };

const sendMessage = async () => {
  if (!input.trim()) return;

  const userMsg = { role: "user", content: input };
  setMessages((prev) => [...prev, userMsg]);
  setInput("");
  setLoading(true);

  try {
    let reply;

    if (!selectedChat || !selectedChat._id) {
      const res = await api.post(
        "/chats/new",
        { message: input },
        { headers: { Authorization: `Bearer ${getToken()}` } }
      );
      setSelectedChat(res.data.chat);
      reply = res.data.reply;
    } else {
      const res = await api.post(
        `/chats/${selectedChat._id}/message`,
        { message: input, sessionId: selectedChat._id },
        { headers: { Authorization: `Bearer ${getToken()}` } }
      );
      reply = res.data.reply;
    }

    // Add AI message with typing animation
    const aiMessageId = Date.now().toString();
    setMessages((prev) => [...prev, { 
      role: "ai", 
      content: reply, 
      id: aiMessageId,
      isTyping: true 
    }]);
    setTypingMessageId(aiMessageId);
    fetchChats();
  } catch (err) {
    console.error("Send message failed:", err);
  } finally {
    setLoading(false);
  }
};



  // Helper function to generate meaningful titles
  const generateMeaningfulTitle = (message) => {
    // Remove common greetings and pleasantries
    const cleaned = message.replace(/^(hi|hello|hey|salam|aoa|asalam|greetings)[,\s]*/i, '');
    // Take the first sentence or first N words
    const title = cleaned.split(/[.!?]/)[0].trim();
    return title.length > 50 ? title.substring(0, 47) + '...' : title;
  };

  return (
    <Layout>
      <ChatSubnav onSelect={(dept)=>setInput(`${dept}: `)} />
      <div className="mx-auto max-w-6xl px-4 py-4 grid grid-cols-1 md:grid-cols-[300px_1fr] gap-4 w-full">
        {/* Sidebar */}
        <aside className="rounded-2xl border border-emerald-900/40 bg-gray-900/60 p-3 flex flex-col h-[72vh]">
          <button onClick={startNewChat} className="mb-2 button-primary w-full">+ New Chat</button>
          
          {/* Active Chats */}
          <div className="space-y-2 overflow-y-auto pr-1 flex-1">
            <h3 className="text-sm font-medium text-gray-400 px-2 mb-2">Active Chats</h3>
            {chats.filter(chat => !chat.archived).map((chat) => (
              <ChatListItem
                key={chat._id}
                chat={chat}
                onSelect={openChat}
                isSelected={selectedChat?._id === chat._id}
                onUpdate={fetchChats}
              />
            ))}
          </div>

          {/* Archived Chats Section */}
          <div className="mt-4">
            <button
              onClick={() => setShowArchived(!showArchived)}
              className="w-full flex items-center justify-between px-2 py-2 text-sm text-gray-400 hover:text-gray-300 transition-colors"
            >
              <span className="flex items-center gap-2">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8" />
                </svg>
                Archived Chats
              </span>
              <svg 
                xmlns="http://www.w3.org/2000/svg" 
                className={`h-4 w-4 transform transition-transform ${showArchived ? 'rotate-180' : ''}`}
                fill="none" 
                viewBox="0 0 24 24" 
                stroke="currentColor"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>
            
            {showArchived && (
              <div className="space-y-2 mt-2 max-h-48 overflow-y-auto pr-1">
                {chats.filter(chat => chat.archived).map((chat) => (
                  <ChatListItem
                    key={chat._id}
                    chat={chat}
                    onSelect={openChat}
                    isSelected={selectedChat?._id === chat._id}
                    onUpdate={fetchChats}
                  />
                ))}
                {chats.filter(chat => chat.archived).length === 0 && (
                  <p className="text-xs text-gray-500 text-center py-2">No archived chats</p>
                )}
              </div>
            )}
          </div>
        </aside>

        {/* Chat area */}
        <section className="rounded-2xl border border-emerald-900/40 bg-gray-900/60 flex flex-col h-[72vh]">
          <div className="flex-1 overflow-y-auto p-4 space-y-3">
            {selectedChat ? (
              <>
                {messages.map((msg, i) => (
                  <div key={msg.id || i} className={`max-w-[80%] rounded-2xl px-4 py-3 ${msg.role === 'user' ? 'ml-auto bg-emerald-600 text-white' : 'bg-gray-800 text-gray-100'}`}>
                    {msg.role === 'ai' && msg.isTyping ? (
                      <TypewriterText 
                        text={msg.content} 
                        speed={25}
                        onComplete={() => {
                          setMessages(prev => prev.map(m => 
                            m.id === msg.id ? { ...m, isTyping: false } : m
                          ));
                          setTypingMessageId(null);
                        }}
                      />
                    ) : (
                      msg.content
                    )}
                  </div>
                ))}
                {loading && <div className="rounded-2xl bg-gray-800 text-gray-400 px-4 py-3 inline-block">Typing...</div>}
              </>
            ) : (
              <div className="h-full grid place-items-center text-gray-500">Select a chat or start a new one</div>
            )}
          </div>
          <div className="border-t border-gray-800 p-3 flex gap-2">
            <input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={(e) => e.key === "Enter" && sendMessage()}
              placeholder="Type your message..."
              className="flex-1 rounded-xl bg-gray-950/60 px-3 py-3 text-sm text-gray-200 outline-none ring-1 ring-gray-800 focus:ring-emerald-700"
            />
            <button onClick={sendMessage} className="button-primary">Send</button>
          </div>
        </section>
      </div>
      <style jsx>{`
        .button-primary { 
          @apply px-4 py-2 rounded-md text-sm font-medium bg-emerald-600 text-white hover:bg-emerald-500 transition-colors; 
        }
      `}</style>
    </Layout>
  );
}
